<template>
  <div class="backdrop" @click.self="toggleShowCart">
    <div class="side-modal">
      <h4 class="modal-title mb-4">Cart</h4>
      <cart-item
        v-for="product in cart"
        :productId="product.id"
        :key="product.id"
        :name="product.name"
        :img="product.img"
        :price="product.price"
        :quantity="product.quantity"
      />
    </div>
  </div>
</template>

<script>
import CartItem from "./CartItem.vue";
import { mapMutations, mapState } from "vuex";
export default {
  name: "CardSideBar",
  components: { CartItem },
  computed: {
    ...mapState(["cart"]),
  },
  methods: {
    ...mapMutations(["toggleShowCart"]),
  },
};
</script>

<style>
.backdrop {
  background: rgba(0, 0, 0, 0.3);
  height: 100vh;
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  padding-left: calc(100% - 240px);
  z-index: 99999;
}
.side-modal {
  background: white;
  padding-top: 1rem;
  height: 100vh;
  box-shadow: -4px 0 16px 5px rgb(0 0 0 / 42%);
}
.modal-title {
  text-align: center;
}
</style>
