import { createStore } from 'vuex'

export default createStore({
  state: {
    cart : [],
    showCart: false
  },
  mutations: {
    initCart(state){
      state.cart = JSON.parse(localStorage.getItem('cart'))
      if(state.cart == null || state.cart[0] == null) state.cart = []
    },
    addToCart(state, payload) {
      const product = state.cart.find( product => { if(product.productId == payload.productId) return product })
      if(product == undefined)
        state.cart.push({...payload,quantity:"1"})
      else 
        product.quantity = parseInt(product.quantity) + 1
      localStorage.setItem('cart', JSON.stringify(state.cart))
    },
    toggleShowCart(state) {
      state.showCart = !state.showCart
    },
    rmProductFromCart(state,id) {
      const product = state.cart.find( product => { if(product.id == id) return product })
      state.cart.splice( state.cart.indexOf(product), 1 )
      localStorage.setItem('cart', JSON.stringify(state.cart))
    }
  },
  getters : {
    numberProductInCart : state => state.cart.reduce( 
        (accumulator,product) => accumulator + parseInt(product.quantity), 0
      )
  }
})