<template>
  <div class="col-md-3 col-sm-6">
    <div class="card product mb-4 box-shadow">
      <img class="card-img-top" :src="`img/${img}`" alt="Card image cap" />
      <div class="card-img-overlay d-flex flex-column">
        <span
          class="card-text position-absolute top-50 start-50 translate-middle bg-red-color p-1 category rounded-3 text-white"
        >
          {{ category }}
        </span>
      </div>
      <div class="card-body">
        <h5 class="card-title">{{ name }}</h5>
        <div class="d-flex justify-content-between align-items-center">
          <div class="btn-group">
            <button
              @click="addToCart({ productId, img, name, price })"
              type="button"
              class="btn btn-sm text-white btn-green-color"
            >
              <i class="bi bi-cart-plus-fill"></i> Add to cart
            </button>
          </div>
          <small class="text-muted"> {{ price }} <span> MAD</span></small>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapMutations } from "vuex";

export default {
  name: "ProductCard",
  props: {
    productId: String,
    img: String,
    name: String,
    category: String,
    price: String,
  },
  methods: {
    ...mapMutations(["addToCart"]),
  },
}
</script>
