<template>
  <i
    @click="toggleShowCart"
    style="cursor:pointer;"
    class="bi bi-cart fs-2 position-relative d-block"
  >
    <span
      class="position-absolute top-50 start-50 translate-middle fs-6 cart--score__number "
    >
      {{ numberProductInCart }}
    </span>
  </i>
</template>

<script>
import { mapGetters, mapMutations } from "vuex";
export default {
  name: "CardNavBar",
  methods: {
    ...mapMutations(["toggleShowCart"]),
  },
  computed: {
    ...mapGetters(["numberProductInCart"]),
  },
};
</script>
