<template>
  <div class="cart-item">
    <img :src="`img/${img}`" alt="Product image" />
    <div class="cart-description">
      <p>
        <strong> {{ name }} </strong>
      </p>
      <p>{{ quantity }} x {{ price }} MAD</p>
    </div>
    <i
      @click="rmProductFromCart(productId)"
      class="fas fa-times text-danger"
    ></i>
  </div>
</template>

<script>
import { mapMutations } from "vuex";
export default {
  name: "BottomNav",
  props: {
    productId: String,
    name: String,
    img: String,
    quantity: String,
    price: String,
  },
  methods: {
    ...mapMutations(["rmProductFromCart"]),
  },
};
</script>

<style>
.cart-item {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 1rem 1.1rem;
  border-bottom: 1px solid #bbb;
}
.cart-item i {
  cursor: pointer;
  margin-left: auto;
}
.cart-item img {
  height: 60px;
  border-radius: 4px;
  /* Temperary */
  filter: contrast(0);
}
.cart-description p {
  margin: 0;
}
</style>
